<i18n>
{
  "en": {
    "PhoneNumber": "Phone number",
    "VerificationCode": "Verification code",
    "Send": "Send",
    "Login": "Login",
    "or": "or"
  },
  "zh": {
    "PhoneNumber": "请输入手机号",
    "VerificationCode": "请输入验证码",
    "Send": "获取验证码",
    "Login": "登录",
    "or": "第三方账号登录"
  }
}
</i18n>

<template>
  <div class="login">
    <div class="back" @click="backHandle">
      <img src="../../assets/global_images/back-icon.png" />
    </div>
    <div class="icon">
      <img src="../../assets/login_images/synermax@3x.png" alt="">
    </div>
    <div class="phone">
      <img src="../../assets/login_images/mobilephone@3x.png" alt="">
      <input type="number"
             v-model="phone"
             oninput="if(value.length>11)value=value.slice(0,11)" :placeholder="$t('PhoneNumber')">
    </div>
    <div class="code">
      <img src="../../assets/login_images/VerificationCode@3x.png" alt="">
      <button @click="getCode"
              :class="{buttonActive: validate.codeActive}"
              :disabled="!validate.codeActive">{{countdownText}}</button>
      <input type="number"
             v-model="code"
             oninput="if(value.length>10)value=value.slice(0,10)" :placeholder="$t('VerificationCode')">
    </div>
    <button class="login_btn"
            @click="login('Phone')"
            :class="{buttonActive: validate.loginActive}" :disabled="!validate.loginActive">{{ $t('Login') }}</button>
    <div class="signIn">
      <div class="description">
        <em></em>
        <span>{{ $t('or') }}</span>
        <em></em>
      </div>
      <ul class="signInIcon">
        <li @click="login('Alipay')">
          <img src="../../assets/login_images/Alipay@3x.png" alt="">
        </li>
        <li @click="login('Wechat')">
          <img src="../../assets/login_images/WeChat@3x.png" alt="">
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'login',
  data () {
    return {
      phone: '', // 手机号码
      code: '', // 手机验证码
      countdown: false, // false 获取验证码 true 倒计时
      timer: ''
    }
  },

  computed: {
    ...mapGetters(['mqttState']),
    // 验证状态
    validate () {
      let vPhone = /^\d{11}$/g.test(this.phone)
      let vText = !this.countdown
      let vCode = /^.{1,}$/g.test(this.code)
      return {
        codeActive: vPhone && vText,
        loginActive: vPhone && vCode
      }
    },

    countdownText () {
      return this.countdown ? this.countdown : this.$t('Send')
    }
  },

  mounted () {
    console.log('// 微信登录33')
    this.dataInteractionResponse(data => {
      if (data.msgType === '0x5104' && data.result) {
        // 微信登录
        console.log('// 微信登录')
        this.setUserInfo(data.data)
        this.setLogin(true)
        console.log(this.mqttState)
        console.log('mqtt状态')
        if (this.mqttState) {
          this.dataInteractionRequest({
            msgType: '0x5151'
          })
          console.log('sss订单查询')
          this.dataInteractionRequest({
            msgType: '0x5221',
            type: 3,
            page: 1,
            count: 10
          })
        }
        this.$router.push('/home')
        console.log('this.$router.push(home)')
      } else if (data.msgType === '0x5106' && data.result) {
        // 获取验证码返回
      } else if (data.msgType === '0x5108' && data.result) {
        // 手机号加验证码登录返回
        if (data.data.isLogin) {
          this.setLogin(true)
          // 查询用户信息请求
          let timer = setInterval(() => {
            if (this.mqttState) {
              clearInterval(timer)
              this.dataInteractionRequest({
                msgType: '0x5151'
              })
            }
          }, 300)
          this.$router.push('/home')
        }
      } else if (data.msgType === '0x510A' && data.result) {
        // 支付宝登录
        this.setUserInfo(data.data)
        this.setLogin(true)
        console.log(this.mqttState)
        console.log('mqtt状态')
        let timer = setInterval(() => {
          if (this.mqttState) {
            clearInterval(timer)
            this.dataInteractionRequest({
              msgType: '0x5151'
            })
            console.log('sss订单查询')
            this.dataInteractionRequest({
              msgType: '0x5221',
              type: 3,
              page: 1,
              count: 10
            })
          }
        }, 300)
        this.$router.push('/home')
      }
    })
  },

  methods: {
    ...mapActions([
      'setUserInfo', 'loadingAlert', 'setLogin'
    ]),

    backHandle () {
      this.$router.back()
    },

    getCode () {
      if (!this.countdown) {
        // 获取验证码请求
        this.dataInteractionRequest({
          msgType: '0x5105',
          phone: this.phone,
          type: 1
        })
        this.countdownHandle()
      }
    },

    // 获取验证码倒计时
    countdownHandle () {
      let num = 60
      this.countdown = `${num}s`
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        this.countdown = `${--num}s`
        if (num === 0) {
          this.countdown = false
          clearInterval(this.timer)
        }
      }, 1000)
    },

    login (type) {
      this.loadingAlert(true)
      switch (type) {
        case 'Phone':
          // 手机号加验证码登录请求
          this.dataInteractionRequest({
            msgType: '0x5107',
            phone: this.phone,
            code: this.code
          })
          break
        case 'Wechat':
          this.dataInteractionRequest({
            msgType: '0x5103'
          })
          break
        case 'Alipay':
          this.dataInteractionRequest({
            msgType: '0x5109'
          })
          break
      }
      if (this.ISDEBUG) {
        let timer = setTimeout(() => {
          this.loadingAlert(false)
          let str = '{"msgType":"0x5104","result":true,"data":{"name":""}}'
          window.dataInteractionResponse(str)
          clearTimeout(timer)
        }, 3000)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.login{
  .fs;
  padding-top: @statusBar;
  background: url('../../assets/login_images/bg.png') 0 0 no-repeat;
  background-size: cover;
  .back{
    .wh(90px, 80px);
    position: absolute;
    left: 2%;
    top: 24px + @statusBar;
    img{
      height: 35px;
      .f-center;
    }
  }
  .icon{
    .wh(300px, 300px);
    margin: 154px auto 0 auto;
    padding-top: 65px;
    img{
      width: 100%;
    }
  }
  .phone, .code{
    .wh(518px, 73px);
    margin: 0 auto;
    position: relative;
    img{
      height: 35px;
      position: absolute;
      left: 0;
      top: 60%;
      margin-top: -17.5px;
    }
    input{
      .wh(90%, 100%);
      float: right;
      font-size: 30px;
      background: transparent;
      border-bottom: 1px solid #999;
      color: #D6D6D6;
      padding-top: 20px;
    }
  }
  .phone{
    margin-top: 88px;
  }
  .code{
    margin-top: 30px;
    input{
      width: 55%;
    }
    button{
      .wh-l(166px, 56px);
      font-size: 24px;
      float: right;
      background: #FFE052;
      color: #FFF;
      border-radius: 10px;
      margin-top: 18px;
      margin-left: 15px;
    }
  }
  .login_btn{
    .wh-l(520px, 80px);
    margin-top: 71px;
    background: #FFE052;
    color: #FFF;
    font-size: 34px;
    border-radius: 10px;
  }
  .buttonActive{
    background: #e7c420 !important;
  }
  .signIn{
    .description{
      .wh(520px, 24px);
      margin: 80px auto 0 auto;
      em{
        .wh(157px, 2px);
        background: #d1d1d3;
        display: block;
        float: left;
        margin-top: 20px;
      }
      span{
        .wh(193px, 24px);
        font-size: 24px;
        color: #cecece;
        float: left;
      }
    }
    .signInIcon{
      .wh(220px, 80px);
      margin: 40px auto 0 auto;
      li:nth-child(1) {
        .wh(80px, 80px);
        float: left;
        img{
          height: 100%;
        }
      }
      li:nth-child(2) {
        .wh(80px, 80px);
        float: right;
        img{
          height: 100%;
        }
      }
    }
  }
}
</style>



